table {
  border: 1px solid grey;
  width: 100%; }
  @media (min-width: 320px) {
    table {
      display: none; } }
  @media (min-width: 768px) {
    table {
      display: block; } }
  @media (min-width: 1366px) {
    table {
      height: 324px;
      width: 1056px;
      border-radius: 6px; } }
  table tr {
    border: 1px solid white; }
    @media (min-width: 1366px) {
      table tr {
        width: 1000px; } }
  table th {
    border-bottom: 1px solid white;
    color: #7E7E82; }
    @media (min-width: 1366px) {
      table th {
        height: 34px; } }
  table td {
    border-bottom: 1px solid grey; }
    @media (min-width: 1366px) {
      table td {
        height: 54px;
        border-color: white;
        width: 250px; } }

.price-cards {
  display: flex;
  flex-wrap: nowrap;
  margin: 0 -5px; }
  @media (min-width: 768px) {
    .price-cards {
      display: none; } }
  .price-cards .price-card {
    height: 200px;
    width: 260px;
    flex-direction: column;
    flex-wrap: wrap;
    flex: 1 0 auto;
    margin: 0 5px;
    border: 1px solid grey;
    border-radius: 6px; }
    .price-cards .price-card p {
      color: #7E7E82; }
      .price-cards .price-card p span {
        color: #24292e; }
