.nav__block {
  display: flex;
  flex-wrap: nowrap;
  overflow: auto;
  margin-bottom: 30px;
  list-style-type: none;
  margin-left: 5px; }
  @media (min-width: 1366px) {
    .nav__block {
      flex-wrap: wrap; } }
  .nav__block li {
    height: 40px;
    width: 248px; }
    @media (min-width: 320px) {
      .nav__block li {
        margin-right: 10px; } }
    .nav__block li a {
      color: #7E7E82;
      text-decoration: none; }
    .nav__block li:active {
      border: 2px solid #B8FFEC;
      display: inline-block;
      border-radius: 6px; }
