.content-menu {
  width: 320px;
  height: auto;
  z-index: 2;
  box-shadow: 6px 0px 4px rgba(14, 24, 80, 0.2), 8px 2px 4px rgba(69, 79, 126, 0.02); }

.feedback {
  display: none;
  width: 320px;
  position: absolute;
  top: 0px;
  right: 0px;
  z-index: 2;
  box-shadow: 6px 0px 4px rgba(14, 24, 80, 0.2), 8px 2px 4px rgba(69, 79, 126, 0.02); }
  @media (min-width: 320px) {
    .feedback {
      height: 676px;
      width: 320px;
      border-radius: 0px; } }
  @media (min-width: 768px) {
    .feedback {
      height: 940px;
      width: 440px; } }
  @media (min-width: 320px) {
    .feedback input {
      margin-right: 50px;
      margin-bottom: 30px;
      height: 48px;
      width: 288px;
      border-radius: 8px; } }
  @media (min-width: 768px) {
    .feedback input {
      margin-right: 50px;
      margin-bottom: 30px;
      height: 48px;
      width: 376px; } }
  @media (min-width: 320px) {
    .feedback textarea {
      margin-right: 50px;
      margin-bottom: 30px;
      height: 118px;
      width: 288px;
      border-radius: 8px; } }
  @media (min-width: 768px) {
    .feedback textarea {
      margin-right: 50px;
      margin-bottom: 30px;
      height: 118px;
      width: 376px; } }

