  @media (min-width: 320px) {
    .call input {
      margin-right: 50px;
      margin-bottom: 30px;
      height: 48px;
      width: 288px;
      border-radius: 8px; } }
  @media (min-width: 768px) {
    .call input {
      margin-right: 50px;
      margin-bottom: 30px;
      height: 48px;
      width: 376px; } }

span {
  color: #FF3E79; }

@media (min-width: 320px) {
  .active-feedback {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    z-index: 3;
    background-color: white;
    width: 100%;
    height: 100%; } }
